import Page from 'components/Page';
import React from 'react';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormFeedback,
  FormGroup,
  FormText,
  Input,
  Label,
  Row,
  Alert,
  Table,
} from 'reactstrap';
import states from "../api/states";
import yearsofex from "../api/yearsofex";
import { newDriver, findDrivers} from "../api/api";
import AsyncStorage from '@callstack/async-storage';
import conf from "../api/config";
import MUIDataTable from "mui-datatables";
import CustomToolbarSelectDrivers from "../components/CustomToolbarSelectDrivers";
class FormPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
          fname : '',
          lname : '',
          address : '',
          city:'',
          state:'nn',
          zip:'',
          email:'',
          phone:'',
          license: '',
          experience: 'nn',
          rate_type: 'nn',
          rate: '',
          xtoken:'',
          msg: '',
          error: '',
          downloadFile:true,
          data: [],
        };
        AsyncStorage.getItem(conf.secret)
        .then((results) => {
          if( results !== null )
          {
            let obje_results = JSON.parse(results);
            this.setState({
              ...this.state,
              xtoken : obje_results.user.token,
            });
            findDrivers(obje_results.user.token).then((response) => {
                // console.log('owners', response.data);
                this.setState({
                    ...this.state,
                    data:response.data,
                });
            }).catch((respError) => {

            });
          }
        })
        .catch((error) => {
          console.log('storage error', error);
        });
    }
    componentDidMount() {
    }

    isValidEmailAddress = (email) => {
        if (/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)) {
            return true;
        }
        return false;
    }

    rerenderParentCallback = () => {
      findDrivers(this.state.xtoken).then((response) => {
        this.setState({
            ...this.state,
            data:response.data,
        });
      }).catch((respError) => {
      });
    }
    
    handleFnameInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, fname: v,});
    }
    handleLnameInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, lname: v,});
    }
    handleAddressInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, address: v,});
    }
    handleCityInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, city: v,});
    }
    handleStateInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, state: v,});
    }
    handleZipInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, zip: v,});
    }
    handleEmailInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, email: v,});
    }
    handlePhoneInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, phone: v,});
    }
    handleLicenseInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, license: v,});
    }
    handleExperienceInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, experience: v,});
    }
    handleRateTypeInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, rate_type: v,});
    }
    handleRateInput = (event) => {
      const v = event.target.value;
      this.setState({...this.state, rate: v,});
    }
    handleSaveChanges = () => {
        // console.log('so far', this.state);
        newDriver(this.state.xtoken, this.state)
        .then((response) => {
            if( response.status === 200)
            {
                this.setState({
                    ...this.state,
                    fname : '',
                    lname : '',
                    address : '',
                    city:'',
                    state:'nn',
                    zip:'',
                    email:'',
                    phone:'',
                    license: '',
                    experience: '',
                    rate_type: '',
                    rate: '',
                    msg: response.message,
                    data: response.data,
                    error: '',
                });

                this.forceUpdate();
            }
            else
            {
                this.setState({
                    ...this.state,
                    error: response.message,
                    msg: '',
                });
            }
        })
        .catch((error) => {
            this.setState({
                ...this.state,
                error: 'Connection error occured',
            });
            console.log('error', error);
        });
    }
    render() {
        const columns = [
            {
              name: 'Id',
              options: {
                display: false
              }
            },
            'First Name',
            'Last Name',
            'Address',
            {
              name: 'City',
              options: {
                display: true
              }
            },
            {
              name: 'State',
              options: {
                display:false,
              }
            },
            {
              name: 'Zip Code',
              options: {
                display: false,
              },
            },
            {
              name: 'Email',
              options: {
                display: true,
              },
            },
            {
              name: 'Phone',
              options: {
                display: true,
              },
            },
            {
              name: 'License',
              options: {
                display: true,
              },
            },
            {
              name: 'Experience',
              options: {
                display: false,
              },
            },
            {
              name: 'Rate Group',
              options: {
                display: false,
                customBodyRender: (value, tableMeta, updateValue) => (
                  value == 1 ? (
                  <Label>Percentage</Label>
                  ):(
                    <Label>Fixed amount</Label>
                  )
                )
              },
            },
            {
              name: 'Rate',
              options: {
                display: false,
              },
            },
        ];

        const data2 = () => {
          let rtn = [];
          this.state.data.map(item => {
            rtn.push([
              item.id,
              item.fname,
              item.lname,
              item.address,
              item.city,
              item.state,
              item.zip,
              item.email,
              item.phone,
              item.license,
              item.experience,
              item.rate_type,
              item.rate,
            ]);
          });
          return rtn;
        }
      
        const options = {
            filter: true,
            selectableRows: 'single',
            selectableRowsOnClick:true,
            filterType: 'dropdown',
            responsive: 'vertical',
            draggableColumns: {
              enabled: true
            },
            rowsPerPage: 5,
            selectToolbarPlacement: 'replace',
            customToolbarSelect: (selectedRows, displayData, setSelectedRows) => (
              <CustomToolbarSelectDrivers rerenderParentCallback={this.rerenderParentCallback} selectedRows={selectedRows} displayData={displayData} setSelectedRows={setSelectedRows}  originalData={this.state.data} Xtoken={this.state.xtoken}/>
            ),
            downloadOptions: {
                filename: 'truck-owners-excel-format.csv',
                separator: ';',
                filterOptions: {
                  useDisplayedColumnsOnly: true,
                  useDisplayedRowsOnly: true,
                }
            },
            onDownload: (buildHead, buildBody, columns, data2) => {
              if (this.state.downloadFile) {
                let val= `${buildHead(columns)}${buildBody(data2)}`.trim();
                return val;
              }
      
              return false;
            },
            onRowSelectionChange: (currentRowsSelected, allRows, rowsSelected) => {
              // console.log('currentRowsSelected', currentRowsSelected);
            },
            onRowsDelete: rowsDeleted => {
              // console.log(rowsDeleted, 'were deleted!');
            },
            onChangePage: numberRows => {
              // console.log(numberRows);
            },
            onSearchChange: searchText => {
              // console.log(searchText);
            },
            onColumnSortChange: (column, direction) => {
              // console.log(column, direction);
            },
            onViewColumnsChange: (column, action) => {
              // console.log(column, action);
            },
            onFilterChange: (column, filters) => {
              // console.log(column, filters);
            },
            onCellClick: (cellIndex, rowIndex) => {
              // console.log(cellIndex, rowIndex);
            },
            onRowClick: (rowData, rowState) => {
              // console.log(rowData, rowState);
            },
        };
      return (
        <Page title="Truck drivers" breadcrumbs={[{ name: 'Drivers', active: true }]}>
          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardHeader>Create Driver</CardHeader>
                {this.state.error.length > 0 && (
                    <Alert color="danger">{this.state.error}</Alert>
                )}
                {this.state.msg.length > 0 && (
                    <Alert color="success">{this.state.msg}</Alert>
                )}
              </Card>
            </Col>
          </Row>

          <Row>
            <Col xl={3} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleEmail">First name*</Label>
                      <Input onChange={this.handleFnameInput}
                        type="text" 
                        value={this.state.fname}
                        name="fname"
                        placeholder="e.g Sam"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleEmail">Last name*</Label>
                      <Input onChange={this.handleLnameInput}
                        type="text" 
                        value={this.state.lname}
                        name="lname"
                        placeholder="e.g Komo"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="examplePassword">Address*</Label>
                      <Input onChange={this.handleAddressInput}
                        type="text"
                        value={this.state.address}
                        name="address"
                        placeholder="e.g. 3015 Lanely park"
                      />
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>

            <Col xl={3} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleUrl">City*</Label>
                      <Input onChange={this.handleCityInput}
                        type="text" 
                        value={this.state.city}
                        name="city"
                        id="city"
                        placeholder="e.g. Plano"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleSelect">State*</Label>
                      <Input onChange={this.handleStateInput} value={this.state.state} defaultValue={this.state.state} type="select" name="state">
                        <option value="nn" disabled={true}>Select state</option>
                        {
                            states.data.map((stt, idx) => (
                                <option key={idx} value={stt.abbreviation}>{stt.name}</option>
                            ))
                        }
                      </Input>
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleNumber">Zip Code*</Label>
                      <Input onChange={this.handleZipInput}
                        type="number"
                        value={this.state.zip}
                        name="zip"
                        id="zip"
                        placeholder="zip code"
                      />
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
    
            <Col xl={3} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="examplePassword">Email*</Label>
                      <Input onChange={this.handleEmailInput}
                        type="email"
                        value={this.state.email}
                        name="email"
                        placeholder="a.valid@email.com"
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="examplePassword">Phone*</Label>
                      <Input onChange={this.handlePhoneInput}
                        type="text"
                        value={this.state.phone}
                        name="phone"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup>
                      <Label for="examplePassword">Drive's license*</Label>
                      <Input onChange={this.handleLicenseInput}
                        type="text"
                        value={this.state.license}
                        name="license"
                        placeholder=""
                      />
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>

            <Col xl={3} lg={12} md={12}>
              <Card>
                <CardBody>
                  <Form>
                    <FormGroup>
                      <Label for="exampleSelect">Years of experience*</Label>
                      <Input onChange={this.handleExperienceInput} value={this.state.experience} defaultValue={this.state.experience} type="select" name="experience">
                        <option value="nn" disabled={true}>Select years</option>
                        {
                            yearsofex.data.map((stt, idx) => (
                                <option key={idx} value={stt.abbreviation}>{stt.name}</option>
                            ))
                        }
                      </Input>
                    </FormGroup>
                    <FormGroup>
                      <Label for="exampleSelect">Rate group*</Label>
                      <Input onChange={this.handleRateTypeInput} value={this.state.rate_type} defaultValue={this.state.rate_type} type="select" name="experience">
                        <option value="nn" disabled={true}>Select rate group</option>
                        <option value="1">Percentage(%) rate</option>
                        <option value="2">Fixed amount($) rate</option>
                      </Input>
                    </FormGroup>
                    <FormGroup>
                      <Label for="examplePassword">Rate value*</Label>
                      <Input onChange={this.handleRateInput}
                        type="number"
                        value={this.state.rate}
                        name="rate"
                        placeholder=""
                      />
                    </FormGroup>
                    <FormGroup check row>
                      <Col sm={{ size: 10, offset: 2 }}>
                        <Button type="button" onClick={this.handleSaveChanges}>Add driver</Button>
                      </Col>
                    </FormGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>

          <Row>
            <Col xl={12} lg={12} md={12}>
              <Card>
                <CardBody>
                    <MUIDataTable title={'Truck drivers'}
                    data={this.state.data.map(item => {
                      return [
                        item.id,
                        item.fname,
                        item.lname,
                        item.address,
                        item.city,
                        item.state,
                        item.zip,
                        item.email,
                        item.phone,
                        item.license,
                        item.experience,
                        item.rate_type,
                        item.rate,
                      ]
                    })} columns={columns} options={options} />
                </CardBody>
              </Card>
            </Col>
          </Row>
    
        </Page>
      );
    }
}
export default FormPage;
