import React from "react";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import CompareArrowsIcon from "@material-ui/icons/CompareArrows";
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import IndeterminateCheckBoxIcon from "@material-ui/icons/IndeterminateCheckBox";
import { withStyles } from "@material-ui/core/styles";
import states from "../api/states";
import yearsofex from "../api/yearsofex";
import { editDriver, deleteDriver } from "../api/api";
import {
  Button,
  ButtonGroup,
  FormGroup,
  Form,
  Label,
  Input,
  Card,
  CardBody,
  CardHeader,
  Col,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row,
  Alert,
} from 'reactstrap';

const defaultToolbarSelectStyles = {
  iconButton: {
  },
  iconContainer: {
    marginRight: "24px",
  },
  inverseIcon: {
    transform: "rotate(90deg)",
  },
};

class CustomToolbarSelectOwners extends React.Component {
  constructor(props)
  {
    super(props);
    this.state = {
        showEditModal:false,
        saved:false,
        id: '',
        fname : '',
        lname : '',
        address : '',
        city:'',
        state:'nn',
        zip:'',
        email:'',
        phone:'',
        license: '',
        experience: 'nn',
        rate_type: 'nn',
        rate: '',
        xtoken:'',
        msg: '',
        error: '',
    };
  }
  hideEditModal = () => {
    this.setState({
      ...this.state,
      showEditModal:false,
    });
    if(this.state.saved)
    {
      this.props.rerenderParentCallback();
    }
  }

    handleEditFnameInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, fname: v,});
    }
    handleEditLnameInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, lname: v,});
    }
    handleEditAddressInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, address: v,});
    }
    handleEditCityInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, city: v,});
    }
    handleEditStateInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, state: v,});
    }
    handleEditZipInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, zip: v,});
    }
    handleEditEmailInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, email: v,});
    }
    handleEditPhoneInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, phone: v,});
    }
    handleEditLicenseInput = (event) => {
        const v = event.target.value;
        this.setState({...this.state, license: v,});
    }
    handleEditExperienceInput = (event) => {
    const v = event.target.value;
    this.setState({...this.state, experience: v,});
    }
    handleEditRateTypeInput = (event) => {
    const v = event.target.value;
    this.setState({...this.state, rate_type: v,});
    }
    handleEditRateInput = (event) => {
    const v = event.target.value;
    this.setState({...this.state, rate: v,});
    }
  handleEditSaveChanges = () => {
      // console.log('id is', this.state);
      editDriver(this.state.xtoken, this.state, this.state.id)
      .then((response) => {
          if( response.status === 200)
          {
            this.setState({
              ...this.state,
              saved:true,
              error: '',
              msg: response.message,
            });
          }
          else
          {
              this.setState({
                  ...this.state,
                  error: response.message,
                  msg: '',
              });
          }
      })
      .catch((error) => {
          this.setState({
              ...this.state,
              error: 'Connection error occured',
          });
          console.log('error', error);
      });
  }
  handleClickEditRow = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    this.setState({
        showEditModal:true,
        id: selectedRowData.id,
        fname : selectedRowData.fname,
        lname : selectedRowData.lname,
        address : selectedRowData.address,
        city:selectedRowData.city,
        state:selectedRowData.state,
        zip:selectedRowData.zip,
        email:selectedRowData.email,
        phone:selectedRowData.phone,
        license: selectedRowData.license,
        experience: selectedRowData.experience,
        rate_type: selectedRowData.rate_type,
        rate: selectedRowData.rate,
        xtoken:this.props.Xtoken,
    });
  }
  handleDeleteSelection = () => {
    let dataIndex = this.props.selectedRows.data[0]['dataIndex'];
    let selectedRowData = this.props.originalData[dataIndex];
    deleteDriver(this.props.Xtoken, selectedRowData.id)
    .then((response) => {
      if(response.status === 200 )
      {
        alert(response.message);
        this.props.rerenderParentCallback();
        return;
      }
      alert(response.message);
      return;
    })
    .catch((error) => {
      alert('Connection error occured');
    });
  };

  handleClickDeselectAll = () => {
    this.props.setSelectedRows([]);
  };

  handleClickBlockSelected = () => {
    console.log(`block users with dataIndexes: ${this.props.selectedRows.data.map(row => row.dataIndex)}`);
  };

  render() {
    const { classes } = this.props;

    return (
      <div className={classes.iconContainer}>

        <Tooltip title={"Edit row"}>
          <IconButton className={classes.iconButton} onClick={this.handleClickEditRow}>
            <EditIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Deselect row"}>
          <IconButton className={classes.iconButton} onClick={this.handleClickDeselectAll}>
            <IndeterminateCheckBoxIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        <Tooltip title={"Delete row"}>
          <IconButton className={classes.iconButton} onClick={this.handleDeleteSelection}>
            <DeleteIcon className={classes.icon} />
          </IconButton>
        </Tooltip>
        {/* modals */}
        <Modal
          size="lg"
          isOpen={this.state.showEditModal}
          backdrop={false}>
          <ModalHeader>
            Edit truck driver
            {this.state.error.length > 0 && (
              <div>
                <br></br><Alert color="danger">{this.state.error}</Alert>
              </div>
            )}
            {this.state.msg.length > 0 && (
              <div>
                <br></br><Alert color="success">{this.state.msg}</Alert>
              </div>
            )}
          </ModalHeader>
          <ModalBody>
            <Row>
                <Col xl={4} lg={12} md={12}>
                <Card>
                    <CardBody>
                    <Form>
                        <FormGroup>
                        <Label for="exampleEmail">First name*</Label>
                        <Input onChange={this.handleEditFnameInput}
                            type="text" 
                            value={this.state.fname}
                            name="fname"
                            placeholder="e.g Sam"
                        />
                        </FormGroup>
                        <FormGroup>
                        <Label for="exampleEmail">Last name*</Label>
                        <Input onChange={this.handleEditLnameInput}
                            type="text" 
                            value={this.state.lname}
                            name="lname"
                            placeholder="e.g Komo"
                        />
                        </FormGroup>
                        <FormGroup>
                            <Label for="examplePassword">Address*</Label>
                            <Input onChange={this.handleEditAddressInput}
                                type="text"
                                value={this.state.address}
                                name="address"
                                placeholder="e.g. 3015 Lanely park"
                            />
                        </FormGroup>
                        <FormGroup>
                            <Label for="exampleUrl">City*</Label>
                            <Input onChange={this.handleEditCityInput}
                                type="text" 
                                value={this.state.city}
                                name="city"
                                id="city"
                                placeholder="e.g. Plano"
                            />
                        </FormGroup>
                    </Form>
                    </CardBody>
                </Card>
                </Col>

                <Col xl={4} lg={12} md={12}>
                <Card>
                    <CardBody>
                    <Form>
                        <FormGroup>
                        <Label for="exampleSelect">State*</Label>
                        <Input onChange={this.handleEditStateInput} value={this.state.state} defaultValue={this.state.state} type="select" name="state">
                            <option value="nn" disabled={true}>Select state</option>
                            {
                                states.data.map((stt, idx) => (
                                    <option key={idx} value={stt.abbreviation}>{stt.name}</option>
                                ))
                            }
                        </Input>
                        </FormGroup>
                        <FormGroup>
                        <Label for="exampleNumber">Zip Code*</Label>
                        <Input onChange={this.handleEditZipInput}
                            type="number"
                            value={this.state.zip}
                            name="zip"
                            id="zip"
                            placeholder="zip code"
                        />
                        </FormGroup>
                        <FormGroup>
                        <Label for="examplePassword">Email*</Label>
                        <Input onChange={this.handleEditEmailInput}
                            type="email"
                            value={this.state.email}
                            name="email"
                            placeholder="a.valid@email.com"
                        />
                        </FormGroup>
                        <FormGroup>
                        <Label for="examplePassword">Phone*</Label>
                        <Input onChange={this.handleEditPhoneInput}
                            type="text"
                            value={this.state.phone}
                            name="phone"
                            placeholder=""
                        />
                        </FormGroup>
                    </Form>
                    </CardBody>
                </Card>
                </Col>
        
                <Col xl={4} lg={12} md={12}>
                <Card>
                    <CardBody>
                    <Form>
                        <FormGroup>
                        <Label for="examplePassword">Drive's license*</Label>
                        <Input onChange={this.handleEditLicenseInput}
                            type="text"
                            value={this.state.license}
                            name="license"
                            placeholder=""
                        />
                        </FormGroup>
                        <FormGroup>
                        <Label for="exampleSelect">Years of experience*</Label>
                        <Input onChange={this.handleEditExperienceInput} value={this.state.experience} defaultValue={this.state.experience} type="select" name="experience">
                            <option value="nn" disabled={true}>Select years</option>
                            {
                                yearsofex.data.map((stt, idx) => (
                                    <option key={idx} value={stt.abbreviation}>{stt.name}</option>
                                ))
                            }
                        </Input>
                        </FormGroup>
                        <FormGroup>
                        <Label for="exampleSelect">Rate group*</Label>
                        <Input onChange={this.handleEditRateTypeInput} value={this.state.rate_type} defaultValue={this.state.rate_type} type="select" name="experience">
                            <option value="nn" disabled={true}>Select rate group</option>
                            <option value="1">Percentage(%) rate</option>
                            <option value="2">Fixed amount($) rate</option>
                        </Input>
                        </FormGroup>
                        <FormGroup>
                        <Label for="examplePassword">Rate value*</Label>
                        <Input onChange={this.handleEditRateInput}
                            type="number"
                            value={this.state.rate}
                            name="rate"
                            placeholder=""
                        />
                        </FormGroup>
                    </Form>
                    </CardBody>
                </Card>
                </Col>
            </Row>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={this.handleEditSaveChanges}>
              Save changes
            </Button>{' '}
            <Button color="secondary" onClick={this.hideEditModal}>
              Close
            </Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default withStyles(defaultToolbarSelectStyles, { name: "CustomToolbarSelectOwners" })(CustomToolbarSelectOwners);